import {
  USER_LOADED,
  USER_LOADING,
  AUTH_ERROR,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  UPDATE_REFRESH,
  LOGOUT,
  OTP_VERIFIED,
  OTP_FAILED,
  ACCESS_TOKEN,
  REFRESH_TOKEN,
} from "../actions/types";

const initialState = {
  access: localStorage.getItem(ACCESS_TOKEN),
  isAuthenticated: null,
  isActive: false,
  isLoading: false,
  user: null,
};

const auth = (state = initialState, action) => {
  switch (action.type) {
    case USER_LOADING:
      return {
        ...state,
        isLoading: true,
      };
    case USER_LOADED:
      return {
        ...state,
        isAuthenticated: true,
        isLoading: false,
        user: action.payload,
      };
    case UPDATE_REFRESH:
      localStorage.setItem(ACCESS_TOKEN, action.payload.access);
      return {
        ...state,
        ...action.payload,
      };
    case LOGIN_SUCCESS:
      localStorage.setItem(ACCESS_TOKEN, action.payload.access);
      localStorage.setItem(REFRESH_TOKEN, action.payload.refresh);
      return {
        ...state,
        ...action.payload,
        isAuthenticated: true,
        isLoading: false,
      };
    case AUTH_ERROR:
    case LOGIN_FAIL:
    case LOGOUT:
      localStorage.removeItem(ACCESS_TOKEN);
      localStorage.removeItem(REFRESH_TOKEN);
      return {
        ...state,
        access: null,
        user: null,
        isAuthenticated: false,
        isLoading: false,
      };
    case OTP_VERIFIED:
      return {
        ...state,
        isActive: true,
      };
    case OTP_FAILED:
      return {
        ...state,
        isActive: false,
      };
    default:
      return state;
  }
};
export default auth;
