import axios from "axios";
import {
  ACCESS_TOKEN,
  REFRESH_TOKEN,
  UPDATE_REFRESH,
  AUTH_ERROR,
} from "./types";
import store from "../store";
const baseURL = "http://localhost:8000/api/";

const axiosInstance = axios.create({
  baseURL: baseURL,
  headers: {
    "Content-Type": "application/json",
  },
});
const axiosIns = axios.create({
  baseURL: baseURL,
  timeout: 3000,
  headers: {
    "Content-Type": "application/json",
  },
});

const access_token = localStorage.getItem(ACCESS_TOKEN);
if (access_token) {
  axiosInstance.defaults.headers.common[
    "Authorization"
  ] = `Bearer ${access_token}`;
} else {
  delete axiosInstance.defaults.headers.common["Authorization"];
}

// Add a request interceptor
axiosInstance.interceptors.request.use(
  async (config) => {
    //   check header config
    // get Token from authorization
    if (access_token) {
      const token = config.headers.common.Authorization.split(" ")[1];
      // decrypt token by atob function
      const tokenParts = JSON.parse(atob(token.split(".")[1]));
      config.headers.post["accept"] = "application/json";

      // exp date in token is expressed in seconds, while now() returns milliseconds:
      const now = Math.ceil(Date.now() / 1000);

      // if token is expired

      if (tokenParts.exp < now) {
        console.log("access token expired");
        const refreshBody = JSON.stringify({
          refresh: window.localStorage.getItem(REFRESH_TOKEN),
        });

        await axiosIns
          .post("/token/refresh/", refreshBody)
          .then((response) => {
            store.dispatch({
              type: UPDATE_REFRESH,
              payload: response.data,
            });

            const accessToken = response.data.access;
            axiosInstance.defaults.headers.common[
              "Authorization"
            ] = `Bearer ${accessToken}`;

            config.headers.Authorization = `Bearer ${accessToken}`;
            return config;
          })
          .catch((error) => {
            store.dispatch({
              type: AUTH_ERROR,
            });
          });
      }
    }
    return config;
  },
  (error) => {
    // Do something with request error
    return Promise.reject(error);
  }
);

export default axiosInstance;
