import React, { Component, lazy, Suspense } from "react";
import { Route, Switch } from "react-router-dom";
import Footer from "./layout/Footer";
import Header from "./layout/Header";
import Test1 from "./pages/question/test";
import PrivateRoute from "./common/PrivateRoute";
const QbankDetails = lazy(() => import("./pages/qbank/details"));
const Home = lazy(() => import("./pages/home/Home"));
const Otp = lazy(() => import("./pages/Otp"));
const QBankPublished = lazy(() => import("./pages/qbank/list_published"));
const Signup = lazy(() => import("./pages/Signup"));
const Login = lazy(() => import("./pages/Login"));
const Profile = lazy(() => import("./pages/profile"));
const MotherSet = lazy(() => import("./pages/motherset"));
const QuestionSet = lazy(() => import("./pages/qbank/question_set"));
const createQuestionBank = lazy(() => import("./pages/qbank/create"));
const CreateQuestion = lazy(() => import("./pages/question/create"));

class Maincontainer extends Component {
  componentDidMount() {
    (function () {
      var headerNode = document.querySelector(".mdk-header");
      var layoutNode = document.querySelector(".mdk-header-layout");
      var componentNode = layoutNode ? layoutNode : headerNode;

      componentNode.addEventListener(
        "domfactory-component-upgraded",
        function () {
          headerNode.mdkHeader.eventTarget.addEventListener(
            "scroll",
            function () {
              var progress = headerNode.mdkHeader.getScrollState().progress;
              var navbarNode = headerNode.querySelector("#default-navbar");

              navbarNode.classList.toggle("bg-transparent", progress <= 0.2);
            }
          );
        }
      );
    })();
  }
  render() {
    return (
      <div className="mdk-header-layout js-mdk-header-layout">
        <Header />

        {/* <!-- Header Layout Content --> */}

        <div className="mdk-header-layout__content page-content">
          <Suspense
            fallback={
              <div className="preloader">
                <div className="sk-double-bounce">
                  <div className="sk-child sk-double-bounce1"></div>
                  <div className="sk-child sk-double-bounce2"></div>
                </div>
              </div>
            }
          >
            <Switch>
              <Route path="/" exact component={Home} />
              <Route path="/login" exact component={Login} />
              <Route path="/register" exact component={Signup} />
              <Route path="/otp" exact component={Otp} />
              <Route path="/test" exact component={Test1} />
              <PrivateRoute path="/motherset" exact component={MotherSet} />
              <PrivateRoute path="/profile" exact component={Profile} />
              <PrivateRoute
                path="/qbank/question-set/create"
                exact
                component={QuestionSet}
              />
              <PrivateRoute
                path="/qbank/create"
                exact
                component={createQuestionBank}
              />
              <PrivateRoute path="/qbank/" exact component={QBankPublished} />
              <PrivateRoute
                path="/qbank/details/:qbank_name"
                exact
                component={QbankDetails}
              />
              <PrivateRoute
                path="/question/create"
                exact
                component={CreateQuestion}
              />
            </Switch>
          </Suspense>
        </div>
        {/* <!-- // END Header Layout Content --> */}

        <Footer />
      </div>
    );
  }
}

export default Maincontainer;
