import axiosInstance from "./axios";
import {
  USER_LOADED,
  USER_LOADING,
  AUTH_ERROR,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  GET_ERRORS,
  LOGOUT,
  OTP_FAILED,
  OTP_VERIFIED,
} from "./types";

//Check token and load user
export const loadUser = () => (dispatch, getState) => {
  //User Loading
  dispatch({ type: USER_LOADING });
  const token = getState().auth.access;
  if (token) {
    axiosInstance
      .get(`/account/user/`)
      .then((res) => {
        dispatch({
          type: USER_LOADED,
          payload: res.data,
        });
      })
      .catch((err) => {
        dispatch({
          type: AUTH_ERROR,
        });
      });
  } else {
    dispatch({
      type: AUTH_ERROR,
    });
  }
};

// Login User
export const login = (username, password) => (dispatch) => {
  // Request body
  const body = JSON.stringify({ username, password });

  axiosInstance
    .post(`/token/`, body)
    .then((res) => {
      dispatch({
        type: LOGIN_SUCCESS,
        payload: res.data,
      });
    })
    .catch((err) => {
      const errors = {
        msg: err.response.data || "Error",
        status: err.response.status,
      };
      dispatch({
        type: GET_ERRORS,
        payload: errors,
      });
      dispatch({
        type: LOGIN_FAIL,
      });
    });
};

// Register User
export const register = (
  first_name = "",
  last_name = "",
  username,
  email = "",
  password,
  phone
) => (dispatch) => {
  // Request body
  const body = JSON.stringify({
    first_name: first_name,
    last_name: last_name,
    username: username,
    email: email,
    password: password,
    phone: phone,
  });

  axiosInstance
    .post(`/account/register`, body)
    .then((res) => {
      console.log(res);
      dispatch({
        type: LOGIN_SUCCESS,
        payload: res.data,
      });
      window.location.href = "/otp";
    })
    .catch((err) => {
      console.log(err.response);
      const errors = {
        msg: err.response.data,
        status: err.response.status,
      };
      dispatch({
        type: GET_ERRORS,
        payload: errors,
      });
    });
};

// Logout User
export const logout = (token) => (dispatch) => {
  // Request body
  const body = JSON.stringify({ refresh_token: token });

  axiosInstance
    .post(`/account/logout/blacklist/`, body)
    .then((res) => {
      dispatch({
        type: LOGOUT,
      });
    })
    .catch((err) => {
      console.log(err);
      const errors = {
        msg: err.response,
        status: err.response.status,
      };
      dispatch({
        type: GET_ERRORS,
        payload: errors,
      });
    });
};

// OTP Verification
export const otp = (otp, id) => (dispatch) => {
  // Request body
  const body = JSON.stringify({ otp: otp, user_id: id });

  axiosInstance
    .post(`/account/otp/`, body)
    .then((res) => {
      console.log(res.data);
      if (res.data.status === 200) {
        dispatch({
          type: OTP_VERIFIED,
        });
        window.location.href = "/";
      } else {
        dispatch({
          type: OTP_FAILED,
        });
      }
    })
    .catch((err) => {
      console.log(err);
      // const errors = {
      //   msg: err.response.data,
      //   status: err.response.status,
      // };
      dispatch({
        type: OTP_FAILED,
      });
    });
};
