import React, { Component } from "react";
import Preloader from "./components/Preloader";
import Sidebar from "./components/Sidebar";
import Maincontainer from "./components/Maincontainer";
import { BrowserRouter } from "react-router-dom";
import $ from "jquery";
import { Provider } from "react-redux";
import store from "./store";
import "jquery";
import { loadUser } from './actions/auth'

class App extends Component {
  componentDidMount() {
    store.dispatch(loadUser());
    
    // Sidebar Toggle
    var t = document.querySelectorAll('[data-toggle="sidebar"]');
    (t = Array.prototype.slice.call(t)).forEach(function (t) {
      t.addEventListener("click", function (t) {
        var n =
          t.currentTarget.getAttribute("data-target") || "#default-drawer";
        var r = document.querySelector(n);
        r && r.mdkDrawer.toggle();
      });
    });
    var n = document.querySelectorAll(".mdk-drawer");
    (n = Array.prototype.slice.call(n)).forEach(function (t) {
      t.addEventListener("mdk-drawer-change", function (t) {
        if (t.target.mdkDrawer) {
          document
            .querySelector("body")
            .classList[t.target.mdkDrawer.opened ? "add" : "remove"](
              "has-drawer-opened"
            );
          var n = document.querySelector(
            '[data-target="#' + t.target.id + '"]'
          );
          n &&
            n.classList[t.target.mdkDrawer.opened ? "add" : "remove"]("active");
        }
      });
    });
    $(".sidebar .collapse").on("show.bs.collapse", function (t) {
      t.stopPropagation();
      $(this)
        .parent()
        .closest("ul")
        .find(".open")
        .find(".collapse")
        .not(this)
        .collapse("hide");
      $(this).closest("li").addClass("open");
    });
    $(".sidebar .collapse").on("hidden.bs.collapse", function (t) {
      t.stopPropagation();
      $(this).closest("li").removeClass("open");
    });

    // Sidebar toggle end
  }
  render() {
    return (
      <Provider store={store}>
        <BrowserRouter>
          <Preloader />
          <Maincontainer />
          <Sidebar />
        </BrowserRouter>
      </Provider>
    );
  }
}

export default App;
