import { BKASH_CREATE_REQ, BKASH_EXECUTE_REQ } from "../actions/types";

const initialState = {
  paymentID: null,
  id_token: null,
  productID: null,
  price: 0,
};

const payment = (state = initialState, action) => {
  switch (action.type) {
    case BKASH_CREATE_REQ:
      return {
        ...state,
        paymentID: action.payload.paymentID,
        id_token: action.payload.id_token,
      };
    case BKASH_EXECUTE_REQ:
      return {
        ...state,
        productID: action.payload.productID,
        price: action.payload.price,
      };
    default:
      return state;
  }
};

export default payment;
