import {
  SCRIPT_LOADER_START,
  SCRIPT_LOADER_LOADING,
  SCRIPT_LOADER_END,
  SCRIPT_LOADER_FAIL,
} from "../actions/types";

const initialState = {
  status: "start",
};

const scriptLoader = (state = initialState, action) => {
  switch (action.type) {
    case SCRIPT_LOADER_START:
      return {
        ...state,
        status: "start",
      };
    case SCRIPT_LOADER_LOADING:
      return {
        ...state,
        status: "loading",
      };
    case SCRIPT_LOADER_END:
      return {
        ...state,
        status: "end",
      };
    case SCRIPT_LOADER_FAIL:
      return {
        ...state,
        status: "fail",
      };
    default:
      return state;
  }
};

export default scriptLoader;
