import React, { Fragment, useEffect } from "react";
import PerfectScrollbar from "perfect-scrollbar";
import $ from "jquery";
import { Link, NavLink, useLocation } from "react-router-dom";
import { useSelector } from "react-redux";

const Sidebar = (props) => {
  const location = useLocation();
  const { user = {}, isAuthenticated = false } = useSelector(
    (state) => state.auth
  );

  useEffect(() => {
    // sidebar scrolling
    $("[data-perfect-scrollbar]").each(function () {
      var t = $(this);
      var e = new PerfectScrollbar(this, {
        wheelPropagation:
          void 0 !== t.data("perfect-scrollbar-wheel-propagation") &&
          t.data("perfect-scrollbar-wheel-propagation"),
        suppressScrollY:
          void 0 !== t.data("perfect-scrollbar-suppress-scroll-y") &&
          t.data("perfect-scrollbar-suppress-scroll-y"),
        swipeEasing: !1,
      });
      Object.defineProperty(this, "PerfectScrollbar", {
        configurable: !0,
        writable: !1,
        value: e,
      });
    });

    // end sidebar scrolling
  }, []);

  const loggedUser = (
    <Fragment>
      <div className="sidebar-heading">Dashboard</div>
      <ul className="sidebar-menu">
        <li
          className={`sidebar-menu-item ${
            location.pathname === "/profile" ? "active" : ""
          }`}
        >
          <NavLink className="sidebar-menu-button" to="/profile">
            <span className="material-icons sidebar-menu-icon sidebar-menu-icon--left">
              account_box
            </span>
            <span className="sidebar-menu-text">Profile</span>
          </NavLink>
        </li>
        <li
          className={`sidebar-menu-item ${
            location.pathname === "/qbank" ? "active" : ""
          }`}
        >
          <NavLink className="sidebar-menu-button" to="/qbank">
            <span className="material-icons sidebar-menu-icon sidebar-menu-icon--left">
              search
            </span>
            <span className="sidebar-menu-text">Question Bank</span>
          </NavLink>
        </li>
      </ul>
    </Fragment>
  );

  const admin = (
    <Fragment>
      <div className="sidebar-heading">Admin Panel</div>
      <ul className="sidebar-menu">
        <li
          className={`sidebar-menu-item ${
            location.pathname === "/motherset" ? "active" : ""
          }`}
        >
          <NavLink className="sidebar-menu-button" to="/motherset">
            <span className="material-icons sidebar-menu-icon sidebar-menu-icon--left">
              school
            </span>
            <span className="sidebar-menu-text">Mother Set</span>
          </NavLink>
        </li>
        <li
          className={`sidebar-menu-item ${
            location.pathname === "/question/create" ? "active" : ""
          }`}
        >
          <NavLink className="sidebar-menu-button" to="/question/create">
            <span className="material-icons sidebar-menu-icon sidebar-menu-icon--left">
              import_contacts
            </span>
            <span className="sidebar-menu-text">Create Question</span>
          </NavLink>
        </li>
        <li
          className={`sidebar-menu-item ${
            location.pathname === "/qbank/create" ? "active" : ""
          }`}
        >
          <NavLink className="sidebar-menu-button" to="/qbank/create">
            <span className="material-icons sidebar-menu-icon sidebar-menu-icon--left">
              help
            </span>
            <span className="sidebar-menu-text">Create Question Bank</span>
          </NavLink>
        </li>
        <li
          className={`sidebar-menu-item ${
            location.pathname === "/qbank/question-set/create" ? "active" : ""
          }`}
        >
          <NavLink
            className="sidebar-menu-button"
            to="/qbank/question-set/create"
          >
            <span className="material-icons sidebar-menu-icon sidebar-menu-icon--left">
              trending_up
            </span>
            <span className="sidebar-menu-text">Create Question Set</span>
          </NavLink>
        </li>

        <li className="sidebar-menu-item">
          <NavLink className="sidebar-menu-button" to="/#">
            <span className="material-icons sidebar-menu-icon sidebar-menu-icon--left">
              post_add
            </span>
            <span className="sidebar-menu-text">Edit Course</span>
          </NavLink>
        </li>
      </ul>
    </Fragment>
  );
  return (
    <div className="mdk-drawer js-mdk-drawer" id="default-drawer">
      <div className="mdk-drawer__content">
        <div
          className="sidebar sidebar-dark-dodger-blue sidebar-left"
          data-perfect-scrollbar
        >
          <Link to="/" className="sidebar-brand">
            <span className="avatar avatar-xl sidebar-brand-icon h-auto">
              <span className="avatar-title rounded">
                <img
                  src="assets/images/logo/alo.png"
                  className="img-fluid"
                  alt="logo"
                />
              </span>
            </span>

            <span>ALO</span>
          </Link>

          <div className="sidebar-heading">Menu</div>
          <ul className="sidebar-menu">
            <li
              className={`sidebar-menu-item ${
                location.pathname === "/" ? "active" : ""
              }`}
            >
              <NavLink className="sidebar-menu-button" to="/">
                <span className="material-icons sidebar-menu-icon sidebar-menu-icon--left">
                  home
                </span>
                <span className="sidebar-menu-text">Home</span>
              </NavLink>
            </li>
          </ul>
          {isAuthenticated ? (
            <Fragment>
              {loggedUser}
              {user
                ? user.profile.role.includes("admin")
                  ? admin
                  : null
                : null}
            </Fragment>
          ) : null}
        </div>
      </div>
    </div>
  );
};

export default Sidebar;
